<template>
  <div class="expense-detail-container">
    <!-- 顶部标题栏 -->
    <div class="header">
      <div class="back-button" @click="goBack">
        <img src="../assets/back.png" alt="返回" class="back-img" />
      </div>
      <div class="title">费用明细</div>
      <div class="more-button">
        <img src="../assets/more-dots-horizontal.png" alt="更多" class="more-img" />
      </div>
    </div>

    <!-- 费用支出详情 -->
    <div class="expense-details">
      <!-- 搜索图标和金额移到这里 -->
      <div class="search-and-amount">
        <div class="search-icon">
          <img src="../assets/report-icon.png" alt="搜索" />
        </div>
        <div class="amount">¥{{ amount }}</div>
      </div>
      
      <div class="detail-header">
        <div class="detail-title">费用支出</div>
        <div class="detail-arrow">
          <img src="../assets/arrow-down.png" alt="展开" />
        </div>
      </div>

      <div class="detail-item">
        <div class="detail-label">地点</div>
        <div class="detail-value">{{ location }}</div>
      </div>

      <div class="detail-item">
        <div class="detail-label">时间</div>
        <div class="detail-value">{{ time }}</div>
      </div>

      <div class="detail-item">
        <div class="detail-label">日期</div>
        <div class="detail-value">{{ date }}</div>
      </div>

      <div class="detail-divider"></div>

      <div class="detail-item">
        <div class="detail-label">花费</div>
        <div class="detail-value">¥{{ amount }}</div>
      </div>

      <div class="detail-item">
        <div class="detail-label">手续费</div>
        <div class="detail-value">- ¥0.00</div>
      </div>

      <div class="detail-item total">
        <div class="detail-label">共计</div>
        <div class="detail-value">¥{{ amount }}</div>
      </div>

      <div class="invoice-button">
        <span>查看发票</span>
      </div>
    </div>

    <div class="voice-button">
      <img src="../assets/mic-icon.png" alt="语音" class="voice-icon-img" />
    </div>

    <!-- 底部导航栏 -->
    <div class="bottom-nav">
      <div class="nav-item active" @click="goToHomePage">
        <div class="nav-icon">
          <img src="../assets/home-icon1.png" alt="首页" class="nav-icon-img" />
        </div>
        <div class="nav-text">首页</div>
      </div>
      <div class="nav-item" @click="goToMyPage">
        <div class="nav-icon">
          <img src="../assets/my-icon1.png" alt="我的" class="nav-icon-img" />
        </div>
        <div class="nav-text">我的</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'ExpenseDetail',
  data() {
    return {
      fromReportView: false,
      amount: '0.00',
      location: '',
      time: '',
      date: ''
    }
  },
  created() {
    // 获取来源信息和费用详情
    this.fromReportView = this.$route.query.fromReportView === 'true';
    this.amount = this.$route.query.amount || '0.00';
    this.location = this.$route.query.location || '';
    this.time = this.$route.query.time || '';
    this.date = this.$route.query.date || '';
  },
  methods: {
    goBack() {
      // 返回账单明细页面
      this.$router.go(-1);
    },
    goToHomePage() {
      // 根据来源返回相应的首页状态
      this.$router.push({
        path: '/home',
        query: { 
          showReportPage: this.fromReportView ? 'true' : 'false'
        }
      });
    },
    goToMyPage() {
      // 返回我的页面，并保持状态
      this.$router.push({
        path: '/my-page',
        query: { 
          fromReportView: this.fromReportView ? 'true' : 'false'
        }
      });
    }
  }
}
</script>

